form {
    width: 90%;
    max-width: 300px;
    background-color: var(--color-1);
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: 24px;
    color: var(--color-3);
    margin-bottom: 26px;
  }
  
  textarea {
    background-color: #F9FBFC;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 20px var(--color-3);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
  }
  
  textarea::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  textarea:focus {
    outline-color: var(--color-2);
  }
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
  }
  
  .TodoForm-button--add {
    background: var(--color-2);
    border-radius: 50px;
    box-shadow: 0px 5px 10px var(--color-2);
    color: white;
  }

  .TodoForm-button--add:hover {
    background: white;
    color: var(--color-2);
  }
  
  .TodoForm-button--cancel {
    background: rgb(115, 65, 65);
    border-radius: 50px;
    box-shadow: 0px 5px 10px rgb(115, 65, 65);
    color: white;
  }

  .TodoForm-button--cancel:hover {
    background: white;
    color: var(--color-2);
  }